.site-nav {
  width: 100% !important;
  position: absolute;
  margin: 0;
  left: -200%;
  z-index: 100;
  top: 100px;
  transition: all 0.2s linear;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out; }
  @media only screen and (min-width: 1170px) {
    .site-nav {
      position: relative;
      top: 10px;
      left: 0; } }
  .site-nav .appear {
    left: 0; }
  .site-nav .menu {
    padding: 0;
    margin: 0;
    font-size: 1.5em;
    line-height: 2em; }
    .site-nav .menu .nav-item {
      list-style: none;
      text-align: center; }
    .site-nav .menu .nav-link {
      padding: 0 1em;
      text-decoration: none;
      display: block;
      border-bottom: 1px solid #666; }
      .site-nav .menu .nav-link:hover {
        background: indigo; }

.icon {
  transition: all 0.3s ease-in;
  font-size: 3em;
  cursor: pointer; }

.move-menu {
  left: 0; }

.fade-me {
  opacity: 0.3; }
