form {
  margin-top: 22px; }
  form img {
    display: block;
    margin: 0 auto; }
  form .question-input {
    border-bottom: 1px dashed #c2c4c2; }
  form input[type=submit] {
    background-color: transparent;
    border: none;
    text-transform: uppercase;
    font-family: "pfdintextcomppro-regular";
    font-size: 18px; }
  form input {
    border: none;
    border-bottom: 1px solid #c2c4c2;
    width: 360px;
    margin: 0 auto;
    display: block;
    height: 60px;
    font-size: 18px; }
    form input:focus {
      box-shadow: none;
      outline: none; }
      form input:focus::-webkit-input-placeholder {
        color: #dbd9d5; }
      form input:focus:-moz-placeholder {
        color: #dbd9d5; }
      form input:focus::-moz-placeholder {
        color: #dbd9d5; }
      form input:focus:-ms-input-placeholder {
        color: #dbd9d5; }
    form input::-webkit-input-placeholder {
      color: #3c3836;
      font-family: "pfdintextcomppro-regular";
      font-size: 18px;
      text-transform: uppercase;
      letter-spacing: 6px; }
    form input:-moz-placeholder {
      color: #3c3836;
      font-family: "pfdintextcomppro-regular";
      font-size: 18px;
      text-transform: uppercase;
      letter-spacing: 6px; }
    form input::-moz-placeholder {
      color: #3c3836;
      font-family: "pfdintextcomppro-regular";
      font-size: 18px;
      text-transform: uppercase;
      letter-spacing: 6px; }
    form input:-ms-input-placeholder {
      color: #3c3836;
