.section-1 {
  display: none;
  min-height: 580px; }
  @media only screen and (min-width: 1024px) {
    .section-1 {
      display: block; } }
  .section-1 .container > h3 {
    margin-top: 121px;
    margin-bottom: 41px;
    letter-spacing: 12px;
    text-align: center;
    font-size: 27px;
    text-transform: uppercase;
    font-family: "pfdintextcomppro-regular"; }
  .section-1 .section-1-footer-conteiner {
    width: 100% !important; }
    .section-1 .section-1-footer-conteiner .footer-container {
      margin: 0 auto;
      width: 1040px; }
  .section-1 p.desc {
    width: 350px;
    margin: 0 auto;
    letter-spacing: 2px;
    margin-bottom: 142px;
    text-align: center;
    color: #656565;
    font-size: 19px;
    font-family: "MinionProIt"; }
  .section-1 .footer {
    border-top: 1px solid #d7d6d3; }
    .section-1 .footer .footer-item {
      border-left: 1px solid #d7d6d3;
      border-right: 1px solid #d7d6d3;
      text-align: center;
      padding-bottom: 20px;
      color: #656565; }
      .section-1 .footer .footer-item:first-child {
        border-left: none; }
      .section-1 .footer .footer-item:last-child {
        border-right: none; }
      .section-1 .footer .footer-item img {
        margin-top: 24px; }
      .section-1 .footer .footer-item h3 {
        font-size: 18px;
        font-family: "pfdintextcomppro-regular";
        line-height: 26px;
        text-transform: uppercase;
        letter-spacing: 4px; }
      .section-1 .footer .footer-item .sub-info {
        font-family: "MinionProIt";
        font-size: 16px; }

.section-2 {
  background-image: url(../images/section-2-bg.png);
  height: 480px;
  display: none; }
  @media only screen and (min-width: 1024px) {
    .section-2 {
      display: block; } }

.about-us-block {
  height: 366px;
  width: 557px;
