.news,
.services {
  text-align: center; }
  .news p.desc,
  .services p.desc {
    width: 340px;
    margin: 0 auto;
    color: #656565;
    font-family: "MinionProIt"; }
    @media only screen and (min-width: 768px) {
      .news p.desc,
      .services p.desc {
